@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Comfortaa&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Oswald:wght@600&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;;
  color: #f1f1f1;
  outline: none !important;
}
*:focus,
*:active {
  outline: none !important;
}
body{
  background-color: rgb(24, 25, 25);
  min-width: 480px;
}
.button{
  background: none;
  border: 1px solid rgb(97, 218, 251);
  margin: 5px 0;
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
}
.button:hover{
  background: rgba(97, 218, 251, 0.1);
}
.input{
  padding: 10px 5px;
  outline: none;
  font-size: 17px;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  display: block;
}
.input:focus {outline: none !important;}
.input::-moz-focus-inner {border: 0px !important;}
.App{
    position: relative;
}